import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { collection, getDocs, query, orderBy } from 'firebase/firestore'; // query와 orderBy 추가
import { db, fstorage } from '../firebase';
import { getDownloadURL, ref } from 'firebase/storage';

function InitialScreen() {
  const navigate = useNavigate();
  const [cracks, setCrackDatas] = useState([]);

  const handleSearch = () => {
    navigate(`/routes`);
  };

  useEffect(() => {
    const fetchCracks = async () => {
      try {
        const q = query(collection(db, 'crack_data'), orderBy('cnt', 'desc')); // 쿼리와 orderBy를 사용하여 cnt 필드 기준으로 내림차순 정렬
        const querySnapshot = await getDocs(q);
        const crackDocsPromises = querySnapshot.docs.map(async (doc) => {
          const crackDatas = doc.data();
          const imageUrl = await getDownloadURL(ref(fstorage, crackDatas.img_path));
          return { id: doc.id, ...crackDatas, imageUrl };
        });
        
        const crackDocs = await Promise.all(crackDocsPromises);
        setCrackDatas(crackDocs);
      } catch (error) {
        console.error('Error fetching reviews:', error);
      }
    };

    fetchCracks();
  }, []);

  return (
    <div className='home_main' style={{ textAlign: 'center', padding: '20px', backgroundColor: '#CBD0D8', minHeight: '100vh' }}>
      <div>
        <h1>Crack</h1>
      </div>
      <div style={{ position: 'absolute', top: 10, right: 10 }}>
        <button onClick={() => navigate('/nearByCrack')}>내 주변 도로 확인하기</button>
      </div>
      <div>
        {true ? (cracks.map((crack) => (
          <div
            key={crack.id}
            style={{
              backgroundColor: 'white',
              padding: '20px',
              margin: '10px auto',
              borderRadius: '10px',
              boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)',
              width: '80%',
              maxWidth: '500px',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}
          >
            <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%' }}>
              <p style={{ margin: '0 0 10px 0', flex: 1 }}>{crack.time}</p>
              <p style={{ margin: '0 0 10px 0', flex: 2 }}>{crack.address}</p>
            </div>
            <img src={crack.imageUrl} alt="Crack Image" style={{ maxWidth: '100%', height: 'auto', aspectRatio: '2 / 1' }} />
          </div>
        ))) : (cracks.map((crack) => (
            <div
              key={crack.id}
              style={{
                backgroundColor: 'white',
                padding: '20px',
                margin: '10px auto',
                borderRadius: '10px',
                boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)',
                width: '80%',
                maxWidth: '500px',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
              }}
            >
              <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%' }}>
                <p style={{ margin: '0 0 10px 0', flex: 1 }}>{crack.time}</p>
                <p style={{ margin: '0 0 10px 0', flex: 2 }}>{crack.address}</p>
                <p style={{ margin: '0 0 10px 0', flex: 2 }}>위도 : {crack.location[0]}  경도 : {crack.location[1]}</p>

              </div>
              <img src={crack.imageUrl} alt="Crack Image" style={{ maxWidth: '100%', height: 'auto', aspectRatio: '2 / 1' }} />
            </div>
          )))}
      </div>
    </div>
  );
}

export default InitialScreen;
