import React, { useState, useEffect } from 'react';
import { db } from '../firebase'; // Firebase 설정 파일을 import
import { collection, getDocs, query, orderBy } from 'firebase/firestore'; // query와 orderBy 추가
import haversine from 'haversine-distance'; // 거리 계산을 위한 라이브러리
import useGeolocation from './useGeolocation';
import '../styles/crack.css'; // CSS 파일 import

const NearbyCracks = () => {
  const [cracks, setCracks] = useState([]);
  const [totalCnt, setTotalCnt] = useState(0);
  const [totalData, setTotalData] = useState(0);

  const now_location = useGeolocation();

  useEffect(() => {
    const fetchCracks = async () => {
      try {
        const q = query(collection(db, 'crack_data'), orderBy('cnt', 'desc')); // 쿼리와 orderBy를 사용하여 cnt 필드 기준으로 내림차순 정렬
        const crackDataSnapshot = await getDocs(q);
        let nearbyCracks = [];
        let cnt = 0;
        let data_num = 0;

        // 가져온 데이터를 순회하면서 처리
        crackDataSnapshot.forEach(async (doc) => {
          const data = doc.data();
          const crackLocation = { latitude: data.location[0], longitude: data.location[1] };

          // haversine 라이브러리를 사용하여 현재 위치와 크랙의 위치 사이의 거리 계산
          const distance = haversine(
            { latitude: now_location.latitude, longitude: now_location.longitude }, // 수정된 부분
            crackLocation
          );

          // 10km 이내의 크랙만 필터링하여 추가
          if (distance <= 10000) { // 10km 이내의 데이터
            nearbyCracks.push({ id: doc.id, ...data });
            cnt += data.cnt;
            data_num += 1;
          }
        });

        // 상태 업데이트: 주변 크랙 정보와 총 위험도 카운트 설정
        setCracks(nearbyCracks);
        setTotalCnt(cnt);
        setTotalData(data_num);
      } catch (error) {
        console.error('Error fetching cracks:', error);
      }
    };

    // 현재 위치(now_location)가 유효할 때 데이터를 가져오도록 함
    if (now_location.latitude && now_location.longitude) {
      fetchCracks();
    }
  }, [now_location]);

  return (
    <div className="container">
      <h1>Crack</h1>
      <div className="header">
        <p>현재 위험도: {totalCnt / totalData}, 총 크랙 수: {totalData}</p>
        <p>위도 {now_location.latitude}</p>
        <p>경도 {now_location.longitude}</p>
      </div>
      <div className="crack-container">
        <h3>내 주변 크랙 위험 구역</h3>
        {cracks.map((crack) => (
          <div key={crack.id} className="crack-card">
            <div className="crack-info">
              <p>{crack.time}</p>
              <p>{crack.address}</p>
            </div>
            <img src={crack.img_path} alt="Crack Image" className="crack-image" />
          </div>
        ))}
      </div>
    </div>
  );
};

export default NearbyCracks;
